.ContactItem{
	position: absolute;
	width: 100vw;
	height: calc(100vh - 50px);
	left: 0;
	bottom: 0;
	background-color: #dbc7c7;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	
}

#telNumber {
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}


