.NewContact {
	padding: 60px 5px 5px 10px;
}

.failureModal,
.successModal {
	position: fixed;
	width: 70%;
	height: 200px;
	left: 15%;
	top: 200px;
	background-color: pink;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column nowrap;
	    flex-flow: column nowrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	z-index: 200;
	border-radius: 5px;
	padding: 10px;
}
.successModal {
	background-color: #c9dbeb;
}
.modalBackdrop{
	position: absolute;
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	background-color: rgba(128,128,128,.5);
	z-index: 100;
}
.NewContact input[type="file"] {
	color: pink;
}


