.MenuList {
	position: absolute;
	width: 50vw;
	height: 100vh;
	top: 0;
	right: 0;
	background-color: black;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: column nowrap;
	    flex-flow: column nowrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	z-index: 200;
	-webkit-transform: translateX(100%);
	    -ms-transform: translateX(100%);
	        transform: translateX(100%);
	-webkit-animation: menu-list-show .5s ease forwards;
	        animation: menu-list-show .5s ease forwards;

}
@-webkit-keyframes menu-list-show {
 0%{
 	-webkit-transform: translateX(100%);
 	        transform: translateX(100%);
 }
 100% {
 	-webkit-transform: translateX(0%);
 	        transform: translateX(0%);
 }
}
@keyframes menu-list-show {
 0%{
 	-webkit-transform: translateX(100%);
 	        transform: translateX(100%);
 }
 100% {
 	-webkit-transform: translateX(0%);
 	        transform: translateX(0%);
 }
}

.MenuList > div {
	width: 100%;
	height: 50px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	color: white;
}

.MenuList a {
	color: white;
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}


